.categoriesContainer {
    // border: green 2px solid;

    margin-top: 90px;
    padding-top: 30px;
    border-top: solid rgba(88,88,88,0.5) 1px;
    list-style: none;

    width: 100%;
    min-height: 300px;

    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    align-items: center;
}

.box {
    height: 235px;
    width: 235px;
    margin: 10px;

    //background
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

    //position
    position: relative;
}

.darkBackground {
    display: flex;
    flex-direction: column;
    justify-content: center;


    background : rgb(64,64,64, 0.8);
    height: 100%;
    width: 100%;

    position: absolute;
    top: 0;
    left: 0;

    // hover animation
    opacity: 0;
    // border-radius: 100%;
    // transform: scale(0.1);

    transition: opacity 0.15s ease-in;
}

a {
    text-decoration: none;
}

.textContainer {
    text-align: center;
    text-decoration: none;
    font-style: normal;

    color: white;
    opacity: inherit;
    z-index: 1;
}

.box:hover {
    .darkBackground {
        opacity: 1;
        // border-radius: 0%;
        // transform: scale(1);
    }
}